.footer_bg {
  background-color: var(--lt-grayish-blue);
}

.footer {
  padding: 3.125rem 0;
}
.footer a {
  color: var(--dark-grayish-blue);
}
@media (max-width: 71.8125em) {
  .footer > div:not(:last-child) {
    margin-bottom: 3rem;
  }
}
.footer__logo {
  font-size: 0;
}
.footer__logo a {
  display: inline-block;
}
.footer__logo img {
  width: 3.5625rem;
}
@media (min-width: 71.875em) {
  .footer {
    display: grid;
    grid-template-columns: 1fr 3fr 2fr;
    justify-items: start;
    align-items: center;
  }
}
.footer__links {
  display: grid;
  grid-template-columns: 1fr;
  gap: 2.0625rem;
}
@media (min-width: 71.875em) {
  .footer__links {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    justify-items: start;
    align-content: space-between;
    column-gap: 4.6875rem;
    row-gap: 0.9375rem;
  }
  .footer__links-faqs {
    grid-column: 1/2;
    grid-row: 1/2;
  }
  .footer__links-contact {
    grid-column: 1/2;
    grid-row: 2/3;
  }
  .footer__links-privacy {
    grid-column: 2/3;
    grid-row: 1/2;
  }
  .footer__links-kit {
    grid-column: 2/3;
    grid-row: 2/3;
  }
  .footer__links-install {
    grid-column: 3/4;
    grid-row: 1/2;
  }
}
.footer__sm-links > a {
  font-size: 1.5625rem;
}
.footer__sm-links > a:not(:last-child) {
  margin-right: 2.25rem;
}
@media (min-width: 71.875em) {
  .footer__sm-links {
    justify-self: end;
  }
}